<style lang="less" scoped>
  .container {
    background-color: #eee;
  } // 头部
  .header {
    display: flex;
    height: 90px;
    background-color: #fff;
    div {
      flex: 2;
      padding-left: 30px;
      height: 90px;
    }
    span {
      flex: 3.333;
      display: inline-block;
      text-align: center;
      height: 90px;
      line-height: 90px;
      &:nth-child(1) {
        color: #999;
        text-align: left;
        text-indent: 20px;
      }
      &:nth-child(2) {
        font-size: 30px;
      }
      &:nth-child(3) {
        text-align: right;
        padding-right: 20px;
        color: #44b336;
        box-sizing: border-box;
      }
    }
  } // 头部
  .area {
    padding: 30px 20px;
    &>div {
      padding-left:  20px;
    }
    &>div {
      background-color: #fff;
      box-sizing: border-box;
      margin-bottom: 20px;
      display: flex;
      span {
        font-size: 30px;
        display: inline-block;
        height: 90px;
        text-align: center;
        line-height: 90px;
        flex: 2;
      }
      input {
        border: none;
        outline: none;
        height: 90px;
        text-indent: 1em;
        flex: 8;
      }
    }
  }
  .submitBtn {
    width: 100%;
    text-align: center;
    &>button {
      width: 90%;
      height: 90px;
      background: #6dce67;
      text-align: center;
      line-height: 90px;
      color: #fff;
      font-size: 35px;
      border: none;
      outline: none;
      border-radius: 20px;
    }
    &>span{
      margin-top: 20px;
      display: inline-block;
      color: #999;
    }
  }
  .yzm {
    position: absolute;
    top: 0;
    right: 20px;
    display: inline-block;
    background-color: transparent;
    color: #44b336;
  }
  #yzm {
    position: relative;
  }
</style>

<template>
  <div class="container">
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <div class="area">
      <div>
        <span>手机号</span>
        <input placeholder="请输入要找回的手机号" type="text" ref="initPhone" v-model="phone" autocomplete="off">
      </div>
      <div id="yzm">
        <span>验证码</span>
        <input placeholder="请输入验证码" type="text" ref="yzm" v-model="yzm" autocomplete="off">
        <span ref="getYzmCode" class="yzm" @click="getCode">获取验证码</span>
      </div>
    </div>
    <div class="submitBtn">
      <button @click="submitBtn">下一步</button>
      <br>
      <span>若手机号未绑定或无法获取验证码请联系班主任老师！</span>
    </div>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        user: {},
        phone: '',
        yzm: '',
        bool: true,
        code: '',
        centerTitle: '手机号找回密码',
        timeInterval: null
      }
    },
    destroyed () {
      this.timeInterval = null
    },
    methods: {
      /**
       * @augments 根据条件查找用户
       */
      findUser() {
        return new Promise((resovle, reject) => {
          this.axios.get('/api/findUser', {
            params: {
              phone: this.phone
            }
          }).then(data => {
            // console.log(data)
            resovle(data)
          }).catch(err => {
            reject(err)
          })
        })
      },
      /**
       * @augments 回到上一个页面
       */
      back() {
        this.$router.go(-1);
      },
      /**
       * @augments 提交按钮,进行下一步
       */
      submitBtn() {
        if (this.phone.length == 0) {
          this.$Toast({
            message: '请输入手机号',
            position: 'middle',
            duration: 1000
          })
          return
        }
        if (this.yzm.length == 0) {
          this.$Toast({
            message: '请输入验证码',
            position: 'middle',
            duration: 1000
          })
          return
        }
        this.axios.post('/api/user/passwdByCompare', {
          comparePasswd: this.yzm,
          comparedPasswd: sessionStorage.getItem('code')
        }).then(data => {
          if (data.data.content) {
            this.$router.push({
              name: 'changePwdPassedPhone',
              params: {
                phone: data.data.content.user.mobile
              }
            })
          } else {
            this.$Toast({
              message: '验证码输入错误',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      /**
       * @augments 获取验证码
       */
      getCode() {
        if (this.phone.length === 0) {
          this.$Toast({
            message: '请填写手机号码',
            position: 'middle',
            duration: 1000
          })
          return
        }
        if (this.bool) {
          this.bool = false
          this.findUser().then(data => {
            if (data.data && data.data.status === 'success') {
              this.axios.post('/api/sendCaptcha', {
                phone: this.phone,
                typeId: data.data.result[0].user.userId,
                type: 'RESET_PWD'
              }).then(data => {
                if (data.data.status !== 'failure') {
                  this.bool = !this.bool
                  var count = 60
                  this.timeInterval = window.setInterval(() => {
                    count--
                    if (count >= 0) {
                      this.$refs.getYzmCode.innerText = `${count}秒后重新获取`
                    } else {
                      clearInterval(this.timeInterval)
                      this.$refs.getYzmCode.innerText = `获取验证码`
                      this.bool = true
                    }
                  }, 1000)
                  sessionStorage.setItem('code', data.data.content)
                } else {
                  this.$Toast({
                    message: data.data.result,
                    position: 'middle',
                    duration: 1000
                  })
                  this.bool = false
                  clearInterval(this.timeInterval)
                  sessionStorage.setItem('code_msg', JSON.stringify(data.data.result))
                }
              }).catch(err => {
                console.log(err)
              })
            }else{
              this.$Toast({
                message: data.data.result,
                position: 'middle',
                duration: 1000
              })
              clearInterval(this.timeInterval)
              sessionStorage.setItem('code_msg', JSON.stringify(data.data.result))
              this.bool = false
            }
          }).catch(err => {
            console.log(err)
          })
        }else{
          // 显示缓存的错误提示内容
          this.$Toast({
            message: JSON.parse(sessionStorage.getItem('code_msg')).errMsg || sessionStorage.getItem('code_msg').replace(/"/g, ''),
            position: 'middle',
            duration: 1000
          })
        }
      }
    },
  }
</script>



